<template>
  <div class="pieChart" />
</template>

<script>
import * as echarts from 'echarts';

// const clientWidth = document.documentElement.clientWidth;
// const clientHeight = document.documentElement.clientHeight;
// const pageWidth = (clientWidth / clientHeight > 16 / 9) ? clientWidth * (16 / 9) : clientWidth;
//
// const this.$px = (n) => n * (pageWidth / 1920);
export default {
    name: 'PieChart',
    mounted () {
        const myChart = echarts.init(document.querySelector('.pieChart'));
        myChart.setOption({
            // 图表标题
            title: {
                text: '月销售目标达成率',
                x: this.$px(35), // 水平安放位置，默认为左对齐，可选为：
                // 'center' ¦ 'left' ¦ 'right'
                // ¦ {number}（x坐标，单位px）
                y: this.$px(31), // 垂直安放位置，默认为全图顶端，可选为：
                // 'top' ¦ 'bottom' ¦ 'center'
                // ¦ {number}（y坐标，单位px）
                // textAlign: null          // 水平对齐方式，默认根据x设置自动调整
                backgroundColor: 'rgba(0,0,0,0)',
                borderRadius: this.$px(5), // 统一设置四个角的圆角大小
                color: '#FFFFFF', // 标题边框颜色
                // borderWidth: this.$px(2), // 标题边框线宽，单位px，默认为0（无边框）
                // padding: [this.$px(4), this.$px(4), this.$px(4), this.$px(14)], // 标题内边距，单位px，默认各方向内边距为5，
                width: this.$px(100),
                height: this.$px(25),
                // 接受数组分别设定上右下左边距，同css
                // itemGap: 10, // 主副标题纵向间隔，单位px，默认为10，
                textStyle: {
                    fontSize: this.$px(22),
                    fontWeight: 'bold',
                    paddingLeft: this.$px(14),
                    color: '#FFF' // 主标题文字颜色
                    // rich: {
                    //     aaa: {
                    //         borderWidth: this.$px(2),
                    //         borderColor: '#f00'
                    //     }
                    // }
                },
                subtextStyle: {
                    color: '#aaa' // 副标题文字颜色
                }
            },
            tooltip: {
                trigger: 'item'
            },
            color: ['#65CFF5', '#8C4BFA'], // 三个数据，三个颜色
            series: [
                {
                    type: 'pie',
                    radius: this.$px(108), // 环形大小
                    center: [this.$px(251), this.$px(221)], // 图形位置
                    itemStyle: {
                        // borderColor: '#fff', //背景图
                        // borderWidth: 1 //border 宽度
                    },
                    label: {
                        alignTo: 'edge',
                        formatter: '{name|{b}}\n{time|{d}%}',
                        minMargin: this.$px(5),
                        edgeDistance: this.$px(10),
                        lineHeight: this.$px(15),
                        padding: [0, this.$px(39)],
                        rich: {
                            name: {
                                fontSize: this.$px(12),
                                color: '#65CFF5'
                            },
                            time: {
                                fontSize: this.$px(16),
                                color: '#fff'
                            }
                        }
                    },
                    labelLine: {
                        length: this.$px(15),
                        length2: 0,
                        maxSurfaceAngle: this.$px(80)
                    },
                    labelLayout: function (params) {
                        var isLeft = params.labelRect.x < myChart.getWidth() / 2;
                        var points = params.labelLinePoints;
                        // Update the end point.
                        points[2][0] = isLeft
                            ? params.labelRect.x
                            : params.labelRect.x + params.labelRect.width;

                        return {
                            labelLinePoints: points
                        };
                    },
                    data: [
                        { value: 1048, name: '本月未完成销售额' },
                        { value: 235, name: '本月完成销售额' }
                    ]
                }
            ]
        });
    }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/helper";
.pieChart{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    //position: relative;
}
</style>
